@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700&display=swap);
:root {
  --black: #212123;
  --grey: #616063;
  --light-brown: #ccb9b1;
  --brown: #c08267;
  --dark-brown: #a0583c;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

body {
  height: 100vh;
}

a:link {
  text-decoration: none;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--brown);
  color: white;
  padding-left: 30px;
  padding-right: 30px;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  left: 0;
}

.header a:visited {
  color: white;
}

.header a:active {
  color: white;
}

.header__logo {
  height: 100%;
  width: 150px;
  cursor: pointer;
}

.header__logo img {
  width: 100%;
  object-fit: cover;
}

.header__nav {
  height: 100%;
  width: 300px;
}

.header__nav-links {
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  height: 100%;
  font-size: 1.5rem;
  font-weight: 500;
}

.header__cart {
  height: 100%;
  display: flex;
  align-items: center;
  font-size: 1.3rem;
  cursor: pointer;
}

.header__cart svg {
  font-size: 2rem;
}

.header__cart-items {
  padding-left: 5px;
}

.header__hamburger {
  cursor: pointer;
  display: none;
}

.header__hamburger svg {
  font-size: 2rem;
}

.header__mobile-links {
  height: 100vh;
  width: 100%;
  position: absolute;
  top: 100%;
  left: 0;
  background-color: var(--grey);
  padding-top: 50px;
  color: white;
  display: flex;
  font-weight: 500;
  flex-direction: column;
  align-items: center;
  font-size: 2rem;
  display: none;
}

@media screen and (max-width: 768px) {
  .header__nav,
  .header__cart {
    display: none;
  }

  .header__hamburger {
    display: block;
  }

  .header__mobile-links {
    display: flex;
  }
  .hide {
    display: none;
    height: 0px;
  }
}

.home {
  height: 850px;
  background-image: url(/shopping-cart/static/media/Hero.0f18ee8b.png);
  background-position: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.home__title {
  color: white;
  text-align: center;
  font-size: 2.7rem;
  max-width: 600px;
  padding-left: 20px;
  padding-right: 20px;
}

.home__title h1 {
  font-weight: bold;
  margin-bottom: 50px;
}

.home__cta-btn {
  border: 0;
  border-radius: 10px;
  color: white;
  background-color: var(--dark-brown);
  padding: 1rem 2rem;
  font-size: 1.8rem;
  cursor: pointer;
  transition: 250ms ease;
}

.home__cta-btn:hover {
  background-color: var(--brown);
}

@media screen and (max-width: 768px) {
  .home {
    height: 80vh;
  }
  .home__title {
    font-size: 1.5rem;
  }
}

.shop {
  padding: 50px 0;
  min-height: 80vh;
}

.shop a {
  color: black;
}
.shop a:visited {
  color: black;
}
.shop a:active {
  color: black;
}

.shop__items {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  justify-content: center;
}

.shop__item {
  width: clamp(350px, 30%, 400px);
  padding: 20px 40px;
}

.shop__card {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 10px;
  border-radius: 5px;
  transition: 100ms ease;
}

.shop__card:hover {
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.12), 0 2px 2px rgba(0, 0, 0, 0.12),
    0 4px 4px rgba(0, 0, 0, 0.12), 0 8px 8px rgba(0, 0, 0, 0.12),
    0 16px 16px rgba(0, 0, 0, 0.12);
}

.shop__card img {
  width: 90%;
  margin-bottom: 20px;
}

.shop__card p {
  font-size: 1.5rem;
}

.cart {
  min-height: 80vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.cart__items {
  width: 100%;
  max-width: 1200px;
  padding: 30px;
  min-height: 100px;
  list-style: none;
}

.cart__item {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  margin-bottom: 30px;
}

.cart__item img {
  height: 200px;
  width: 200px;
  margin-bottom: 20px;
  object-fit: cover;
}

.cart__item-details {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex: 1 1;
  padding-left: 30px;
  flex-wrap: wrap;
  min-width: 350px;
}

.cart__item-details span {
  margin-left: 10px;
  margin-right: 10px;
  font-size: 1.5rem;
}

.cart__item-details svg {
  font-size: 2rem;
  cursor: pointer;
}

.cart__item-details h3 {
  max-width: 400px;
}

.cart__item-quantity {
  padding-top: 10px;
}

.cart__item-quantity label {
  padding-right: 10px;
}

.cart__item-quantity button {
  cursor: pointer;
  font-size: 1.3rem;
  color: white;
  background-color: var(--grey);
  height: 40px;
  width: 40px;
  border: 0;
  border-radius: 50%;
}

.cart__total {
  text-align: center;
  padding: 0 30px 30px 30px;
}

.cart__total p {
  font-size: 2rem;
  font-weight: 500;
  padding-bottom: 20px;
}

.cart__total button {
  border: 0;
  border-radius: 10px;
  padding: 10px 25px;
  font-size: 1.8rem;
  background-color: var(--dark-brown);
  color: white;
  cursor: pointer;
}

.cart__empty {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 3rem;
  text-align: center;
  padding-top: 150px;
}

.cart__empty svg {
  font-size: 6rem;
}

@media screen and (max-width: 768px) {
  .cart__empty {
    padding-top: 100px;
  }
}

@media screen and (max-width: 610px) {
  .cart__item-details {
    padding-left: 0;
  }
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="number"] {
  width: 50px;
  padding: 5px;
  margin-left: 10px;
  margin-right: 10px;
  text-align: center;
  font-size: 1rem;
}

.details {
  padding-top: 100px;
  padding-bottom: 100px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.details img {
  width: 500px;
  height: 550px;
  object-fit: cover;
}

.details__info {
  padding: 20px 45px;
  width: 500px;
}

.details__info span {
  font-size: 1.4rem;
}

.details__quantity {
  padding-top: 10px;
}

.details__quantity label {
  padding-right: 10px;
}

.details__quantity button {
  cursor: pointer;
  font-size: 1.3rem;
  color: white;
  background-color: var(--grey);
  height: 40px;
  width: 40px;
  border: 0;
  border-radius: 50%;
}

.details__btns {
  padding-top: 20px;
}

.details__btns a {
  text-decoration: none;
  color: white;
  padding: 15px 30px;
  margin-right: 20px;
  border-radius: 10px;
  background-color: var(--dark-brown);
}

.details__btns button {
  padding: 15px 20px;
  border: 0;
  border-radius: 10px;
  font-size: inherit;
  cursor: pointer;
  color: white;
  background-color: var(--grey);
}

.error {
  height: 80vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 3rem;
  text-align: center;
}

@media screen and (max-width: 600px) {
  .details img {
    width: 350px;
    height: 450px;
    object-fit: cover;
  }

  .details__info {
    width: 350px;
    padding: 40px 0 0 0;
    text-align: center;
  }
}

.footer {
  padding: 30px 30px 50px 30px;
  background-color: var(--black);
  color: white;
  display: flex;
  align-items: center;
  flex-direction: column;
  font-size: 2.5rem;
  font-weight: bold;
}

.footer__icons {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 200px;
  margin-bottom: 30px;
}

.footer__icon {
  transition: 100ms linear;
  cursor: pointer;
}

.footer__icon:hover {
  transform: translateY(-7px);
}

@media screen and (max-width: 768px) {
  .footer span {
    font-size: 1.5rem;
  }
}

